export default {
  namespaced: true,
  state: {
    // ...demo, // REMOVE ON PRODUCTION
    profile: null,
    messages: [],
    passwordResetToken: '',
  },
  actions: {
    logout: ({ state }, dontRedirect) => {
      state.token = null
      state.profile = null

      sessionStorage.removeItem('auth-token')
      if (dontRedirect) router.push({ path: '/' })
    },
    getAuthorized(
      { commit, state },
      { profile, token, messages, notifyAboutNewMessage },
    ) {
      commit('SET_PROFILE', {
        profile,
        token,
      })

      var notify = notifyAboutNewMessage && profile?.newMessages

      if (
        profile &&
        typeof messages?.length != 'undefined' &&
        messages?.length != state.messages.length
      ) {
        messages.forEach(m => {
          m.yours = !m.yours
        })
        commit('SET_MESSAGES', messages)

        if (notify) {
          let latest = messages[messages.length - 1]
          let message = latest.text

          if (message.length > 150) {
            message = 'You have got new message'
          } else if (profile.newMessages > 1) {
            message = 'You have got new messages'
          }

          document.querySelector('.new-message-sound').play()
          commit(
            'popups/ADD_ALERT',
            {
              type: 'message',
              title: 'Support',
              message,
            },
            { root: true },
          )
        }
      }
    },
  },
  mutations: {
    SET_PROFILE: (state, { token, profile }) => {
      state.token = token

      if (token) sessionStorage.setItem('auth-token', token)
      if (profile) state.profile = profile
    },
    SET_MESSAGES(state, messages) {
      state.messages = messages
    },
    CHANGE_NAME: (state, { first, last }) => {
      if (first) state.profile.firstName = first
      if (last) state.profile.lastName = last
    },
    UPDATE_WALLETS: (state, wallets) => {
      if (state.profile) {
        state.profile.wallets = wallets
      }
    },
    ADD_TRANSACTION: (state, transaction) => {
      if (state.profile) {
        state.profile.transactions.push(transaction)
      }
    },
    RESET_PASSWORD(state, token) {
      if (!state.profile || !token) state.passwordResetToken = token
    },
  },
  getters: {
    isLoggedIn: state => Boolean(state.profile),
    profile: state => state.profile,
    messages: state => state.messages,
    newMessages: state => state.newMessages,
    passwordResetToken: state => state.passwordResetToken,
    transactions: state =>
      state.profile
        ? state.profile.transactions.filter(
            t => t.name !== 'Deposit' && t.name !== 'Withdrawal',
          )
        : [],
    transfers: state =>
      state.profile && state.profile.transactions
        ? state.profile.transactions
            .filter(t => t.name === 'Transfer')
            .sort((ta, tb) => (ta.at > tb.at ? -1 : ta.at < tb.at ? 1 : 0))
        : [],
    withdrawals: state =>
      state.profile && state.profile.transactions
        ? state.profile.transactions
            .filter(t => t.name === 'Withdrawal')
            .sort((ta, tb) => (ta.at > tb.at ? -1 : ta.at < tb.at ? 1 : 0))
        : [],
    deposits: state =>
      state.profile && state.profile.transactions
        ? state.profile.transactions
            .filter(t => t.name === 'Deposit')
            .sort((ta, tb) => (ta.at > tb.at ? -1 : ta.at < tb.at ? 1 : 0))
        : [],
    wallets: state => (state.profile ? state.profile.wallets : {}),
  },
}
