<template>
  <div class="support">
    <div class="support__title">
      <font-awesome-icon
        icon="times"
        class="support__title-close"
        @click="close"
      />
      <font-awesome-icon
        icon="user"
        class="support__title-icon"
        style="color: #0075ff"
      />
      <p class="support__title-text">Support chat</p>
    </div>
    <div class="support__chat">
      <div
        class="support__chat_body"
        :style="{
          position: !!messages.length ? 'relative' : 'initial',
        }"
      >
        <div
          class="empty"
          :style="{
            opacity: +!messages.length,
          }"
        >
          <font-awesome-icon
            icon="life-ring"
            class="empty-icon"
            style="color: #0075ff33"
          />
          You have troubles? Text us!
        </div>
        <div
          v-for="(message, i) in messages"
          :key="i"
          :style="{
            paddingTop: !message.delivered ? '1.2rem' : '0.657rem',
          }"
          :class="[
            !message.newDivider
              ? 'support__chat_body_message'
              : 'support__chat_body_divider',
            message.yours ? 'support__chat_body_message-yours' : '',
          ]"
        >
          <font-awesome-icon
            class="support__chat_body_message-info"
            v-if="!message.delivered && !message.newDivider"
            :style="{
              left: message.yours ? 'auto' : '0.5rem',
              right: message.yours ? '0.5rem' : 'auto',
            }"
            icon="spinner"
            spin
          ></font-awesome-icon>
          {{ !message.newDivider ? message.text : 'New messages' }}
          <span
            class="support__chat_body_message-date"
            v-if="message.delivered && !message.newDivider"
            :style="{
              opacity: 0.4,
              textAlign: message.yours ? 'right' : 'left',
            }"
          >
            {{ message.date }}
          </span>
        </div>
      </div>
      <div class="support__chat_input">
        <input
          v-model="messageInput"
          placeholder="Enter you message..."
          type="text"
          @keydown.enter="sendMessage"
        />
        <button @click="sendMessage">send</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import axios from 'axios'

export default {
  name: 'Support',
  props: {
    show: Boolean,
  },
  data: () => ({
    messageInput: '',
    messages: [],
  }),
  computed: {
    ...mapGetters({
      profile: 'auth/profile',
      profileMessages: 'auth/messages',
    }),
  },
  watch: {
    profileMessages(v) {
      this.messages = v

      if (!this.show) {
        var length = this.messages.length
        var newItems = this.profile ? this.profile.newMessages : 0

        if (
          length > newItems &&
          newItems &&
          !this.messages.filter(m => m.newDivider).length
        ) {
          this.messages.splice(length - newItems, 0, {
            newDivider: true,
          })
        }
      }
    },
    messages() {
      setTimeout(() => {
        this.scrollToLast()
      }, 10)
    },
    show(yes) {
      if (yes) {
        document.body.style.overflowY = 'hidden'
        this.$el.style.opacity = 1
        this.$el.style.pointerEvents = 'all'
        this.fetchMessages()
        if (this.profile) this.profile.newMessages = 0
      } else {
        if (this.$route.name != 'Main') document.body.style.overflowY = 'auto'
        this.$el.style.opacity = 0
        this.$el.style.pointerEvents = 'none'
      }
    },
  },
  methods: {
    fetchMessages() {
      const base = window.location.protocol + '//' + window.location.host

      axios
        .get(base + '/api/support', {
          headers: {
            Authorization: sessionStorage.getItem('auth-token'),
          },
        })
        .then(() => {
          if (this.show) setTimeout(this.fetchMessages, 2000)
        })
        .catch(err => {
          console.log(err)
        })
    },
    scrollToLast() {
      setTimeout(() => {
        this.$el.querySelector('.support__chat_input input').focus()
        const msgs = this.$el.querySelectorAll('.support__chat_body_message')

        if (!msgs.length) {
          return
        } else {
          const last = msgs[msgs.length - 1]
          last.parentNode.scrollTop = last.offsetTop
        }
      }, 10)
    },
    sendMessage() {
      let i = this.messages.length

      this.messages.push({
        yours: true,
        text: this.messageInput,
        delivered: false,
      })

      this.axios
        .post(
          window.location.protocol +
            '//' +
            window.location.host +
            '/api/support',
          {
            token: sessionStorage.getItem('auth-token'),
            message: this.messageInput,
          },
        )
        .then(({ data }) => {
          this.messages[i].delivered = true
          this.messages[i].date = data.message.date
          this.$store.commit('auth/SET_MESSAGES', this.messages)
          this.scrollToLast()
        })
        .catch(err => {
          if (err.response) {
            this.handleError(err.response)
          }
        })

      this.messageInput = ''
      this.scrollToLast()
    },
    handleError(err) {
      console.log(err)
    },
    close() {
      this.$store.commit('popups/SUPPORT', false)
      setTimeout(() => {
        this.messages = this.messages.filter(m => !m.newDivider)
      }, 1000)
    },
  },
  mounted() {
    this.scrollToLast()
  },
}
</script>

<style lang="sass" scoped>
@import "../../scss/smart-grid"
@import "../../scss/variables"

$chat-message-br: 0.65rem
$chat-padding: 10px
$mobile: 55rem

.support
  position: absolute
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  z-index: 200
  top: 0
  left: 0
  opacity: 0
  width: 100vw
  height: 100vh
  background-color: #00002299
  backdrop-filter: blur(2px)
  pointer-events: none
  transition: opacity .4s
  &__title
    height: 30px
    display: flex
    align-items: center
    justify-content: center
    padding: 10px
    background-color: $blue
    border-radius: 5px 5px 0 0
    border: 1px $cyan-alpha solid
    position: relative
    @include to($mobile)
      width: 90vw
    @include from($mobile)
      width: 50vw
    &-icon
      margin-right: 10px
    &-close
      position: absolute
      height: 100%
      right: 20px
      justify-self: end
      color: #0075ff
      cursor: pointer
      font-size: 1.1rem

      &:hover
        color: white
    &-text
      color: white
  &__chat
    background-color: #001155aa
    padding: $chat-padding
    z-index: 200
    width: 50vw
    height: 50vh
    display: flex
    flex-direction: column
    border-radius: 0 0 5px 5px
    border: 1px $blue solid

    @include to($mobile)
      width: 90vw
      height: 80vh
    @include from($mobile)
      width: 50vw
      height: 50vh
    &_body
      font-family: 'Montserrat'
      width: calc(100% - 20px)
      height: 100%
      border-radius: 10px
      display: flex
      flex-direction: column
      align-items: flex-end
      overflow-y: auto
      padding: 20px 10px
      font-size: .9rem
      position: relative

      .empty
        color: $cyan-alpha
        position: absolute
        width: 100%
        height: 100%
        top: 0
        left: 0
        text-align: center
        display: grid
        align-content: center
        transition: all 1s
        user-select: none
        pointer-events: none
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center

        &-icon
          font-size: 5rem
          margin-bottom: 20px

        @include to($mobile)
          font-size: 1rem
        @include from($mobile)
          font-size: 1.4rem

      &_divider
        width: 100%
        text-align: center
        color: $cyan-alpha
        border-bottom: 1px $cyan-alpha solid
        margin-bottom: 15px
        user-select: none
        cursor: default

      &_message
        color: white
        position: relative
        padding: 0.657rem 1.375rem
        margin-bottom: $chat-message-br
        max-width: 70%
        overflow-wrap: break-word
        border-radius: 0.5rem
        border-top-left-radius: 0
        align-self: flex-start
        background-color: $light-blue
        text-align: justify
        transition: padding 0.3s
        display: flex
        flex-direction: column

        &-yours
          background-color: $cyan
          align-self: flex-end
          border-top-right-radius: 0
          border-top-left-radius: 0.5rem

        &-info
          position: absolute
          top: 0.4rem
          font-size: 0.6rem

        &-date
          font-family: sans-serif
          font-size: 0.7rem
          opacity: 0
          transition: opacity 0.5s
          margin-top: .5rem

    &_input
      width: 100%
      margin-top: 1.3rem
      display: flex

      input, button
        font-family: 'Montserrat'
        color: white
        border-radius: 5px
        outline: none
        border: none

      input
        margin-right: 1rem
        width: 100%
        min-width: 200px
        font-size: .9rem
        letter-spacing: .05rem
        padding: 10px
        background-color: $blue
        color: $light-blue
        transition: box-shadow .2s
        box-shadow: inset 0 0 1px $cyan-alpha

        &:focus
          box-shadow: inset 0 0 8px $cyan-alpha

        &::placeholder
          color: $cyan-alpha

      button
        text-transform: uppercase
        width: 100px
        background-color: $cyan-alpha
        transition: box-shadow .3s, background-color .3s

        &:hover
          box-shadow: 0 2px 15px $cyan-alpha
          background-color: $cyan
          cursor: pointer
        &:active
          background-color: $light-blue
</style>
