Vue.prototype.$time = {
  getLocale: () => {
    return +new Date()
  },
  getPacific: () => {
    let offset = new Date().getTimezoneOffset() * 60 * 1000
    return +new Date() + offset
  },
  getExpiration: (from, additionalMinutes) => {
    let min = typeof additionalMinutes === 'number' ? additionalMinutes : 0
    return from + 1000 * 60 * min
  },
  localeToPacific: locale => {
    let offset = new Date().getTimezoneOffset() * 60 * 1000
    return locale + offset
  },
  pacificToLocale: pacific => {
    let offset = new Date().getTimezoneOffset() * 60 * 1000
    return pacific - offset
  },
}

Vue.prototype.$axios = {
  post: (api, body) =>
    axios.post(
      window.location.protocol + '//' + window.location.host + api,
      body,
      {
        headers: {
          Authorization: sessionStorage.getItem('auth-token'),
        },
      },
    ),
  get: api =>
    axios.get(window.location.protocol + '//' + window.location.host + api, {
      headers: {
        Authorization: sessionStorage.getItem('auth-token'),
      },
    }),
}

Vue.prototype.$anime = anime
Vue.prototype.$nanoid = nanoid
Vue.prototype.$moment = moment

Vue.prototype.$copy = text => {
  let dummy = document.createElement('textarea')
  document.body.appendChild(dummy)
  dummy.value = text
  dummy.select()
  document.execCommand('copy')
  document.body.removeChild(dummy)
}

Vue.prototype.$isDesktop = !navigator.userAgent.match(
  /(iPhone|iPod|iPad|Android|BlackBerry|IEMobile)/,
)

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
